import React, { useState } from 'react';
import { getEligibleParents } from './utils';

// Componente AddProcessForm: Formulrio para adicionar novos processos ou tarefas.
// Props:
// - processes: lista de processos existentes
// - currentLevel: nvel atual no qual o novo processo ser adicionado
// - setCurrentLevel: funo para atualizar o nvel atual
// - addProcess: funo para adicionar um novo processo
// - t: objeto de tradues para suporte multilngue
const AddProcessForm = ({ processes, currentLevel, setCurrentLevel, addProcess, t }) => {
  // Estados para armazenar o nome do processo, o processo pai selecionado, e possveis erros
  const [processName, setProcessName] = useState('');
  const [parentProcess, setParentProcess] = useState('');
  const [error, setError] = useState('');

  // Funo para lidar com a adio de um novo processo
  const handleAddProcess = () => {
    setError(''); // Reseta o erro antes de qualquer validao

    // Verifica se o nome do processo no est vazio
    if (processName.trim() === '') {
      setError('Process name cannot be empty.'); // Define uma mensagem de erro
      return;
    }
    
    // Se o nvel atual no for o nvel 1 (Macroprocesso), verifica se um processo pai foi selecionado
    if (currentLevel !== '1' && !parentProcess) {
      setError('Please select a parent process.'); // Define uma mensagem de erro
      return;
    }
    
    // Adiciona o processo utilizando a funo addProcess
    addProcess(processName, parentProcess);
    
    // Reseta os campos do formulrio aps adicionar o processo
    setProcessName('');
    setParentProcess('');
  };

  return (
    <div className="w-full bg-gray text-white">
      {/* Seleo do nvel do processo */}
      <div className="mb-4">
        <select
          value={currentLevel}
          onChange={(e) => {
            setCurrentLevel(e.target.value); // Atualiza o nvel atual
            setParentProcess(''); // Reseta o processo pai quando o nvel muda
            setError(''); // Reseta os erros quando o nvel muda
          }}
          className="w-full bg-black border border-green-700 rounded-lg text-white p-2"
        >
          <option value="1">1. Business Process (Macroprocess)</option>
          <option value="2">2. Process</option>
          <option value="3">3. Sub-Process</option>
          <option value="4">4. Activity</option>
          <option value="5">5. Task</option>
        </select>
      </div>
      
      {/* Seleo do processo pai (aparece apenas se o nvel atual no for 1) */}
      {currentLevel !== '1' && (
        <div className="mb-4">
          <select
            value={parentProcess}
            onChange={(e) => setParentProcess(e.target.value)} // Atualiza o processo pai selecionado
            className="w-full bg-black border border-green-700 text-white p-2 rounded mb-4"
          >
            <option value="">{t.selectParent}</option> {/* Placeholder para selecionar o processo pai */}
            {/* Mapeia e exibe os processos elegveis como pais */}
            {getEligibleParents(processes, currentLevel).map((process, index) => (
              <option key={index} value={process.id}>{process.name}</option>
            ))}
          </select>
        </div>
      )}
      
      {/* Campo de entrada para o nome do processo e boto para adicionar */}
      <div className="flex mb-4">
        <input
          value={processName}
          onChange={(e) => setProcessName(e.target.value)} // Atualiza o nome do processo
          placeholder={t.placeholder} // Placeholder baseado na traduo
          className="flex-1 bg-black border border-green-900 text-white p-2"
        />
        <button
          onClick={handleAddProcess} // Chama a funo handleAddProcess ao clicar
          className="bg-green-700 hover:bg-green-500 text-white p-2 rounded"
        >
          {t.addProcess} {/* Texto do boto baseado na traduo */}
        </button>
      </div>

      {/* Exibe mensagens de erro, se houver */}
      {error && (
        <div className="mb-4 bg-red-900 border border-red-700 text-white p-2 rounded">
          {error}
        </div>
      )}
    </div>
  );
};

export default AddProcessForm; // Exporta o componente para uso em outras partes da aplicao
