import React from 'react';

// Componente de carto reutilizvel que pode ser usado como um continer estilizado.
// Recebe os filhos (children) para renderizar dentro do carto e uma classe CSS opcional (className) para personalizao adicional.
export const Card = ({ children, className }) => (
  <div className={`bg-[#1a1a1a] border border-[#333333] rounded-lg shadow-lg ${className}`}>
    {children}
  </div>
);

// Componente para o cabealho do carto, que adiciona padding e uma borda inferior para separar do contedo do carto.
// Recebe os filhos (children) para renderizar dentro do cabealho.
export const CardHeader = ({ children }) => (
  <div className="p-4 border-b border-[#333333]">
    {children}
  </div>
);

// Componente para o contedo do carto, que adiciona padding ao redor dos elementos dentro do carto.
// Recebe os filhos (children) para renderizar dentro do contedo.
export const CardContent = ({ children }) => (
  <div className="p-4">
    {children}
  </div>
);

// Componente para o ttulo do carto, estilizado com uma fonte grande e negrito.
// Recebe os filhos (children), que geralmente sero o texto do ttulo.
export const CardTitle = ({ children }) => (
  <h2 className="text-xl font-bold text-[#f1f5f9]">
    {children}
  </h2>
);

// Componente de input reutilizvel, que representa um campo de entrada de texto.
// Todos os atributos (props) do input so passados diretamente para o elemento input HTML.
// Estilizado para ter uma aparncia consistente em todo o projeto.
export const Input = ({ ...props }) => (
  <input
    {...props}
    className="w-full bg-[#111111] text-gray-300 border border-[#333333] focus:border-[#00ff9d] focus:ring-[#00ff9d] rounded-md p-2"
  />
);

// Componente de input de data reutilizvel, semelhante ao componente Input, mas com o tipo de input definido como 'date'.
// Todos os atributos (props) do input so passados diretamente para o elemento input HTML.
export const DateInput = ({ ...props }) => (
  <input
    {...props}
    type="date"
    className="w-full bg-[#111111] text-gray-300 border border-[#333333] focus:border-[#00ff9d] focus:ring-[#00ff9d] rounded-md p-2"
  />
);

// Componente de boto reutilizvel, com suporte para um estado 'selecionado'.
// Quando o boto est selecionado, ele muda de cor para indicar ao usurio.
// Recebe os filhos (children) para renderizar dentro do boto e quaisquer outros atributos (props) que sero passados para o boto HTML.
export const Button = ({ children, selected, ...props }) => (
  <button
    {...props}
    className={`bg-[#374151] hover:bg-[#00864c] text-white font-medium py-2 px-4 rounded transition-colors duration-300 flex items-center ${
      selected ? 'bg-[#00ff9d] text-black' : ''
    }`}
  >
    {children}
  </button>
);

// Componente de linha do tempo (Timeline), que exibe uma srie de crculos representando o progresso atravs das perguntas.
// O crculo muda de cor quando a pergunta correspondente foi alcanada ou superada.
// - currentQuestion: ndice da pergunta atual, usado para determinar o progresso.
// - questions: uma lista de perguntas (ou placeholders), usada para gerar os crculos.
export const Timeline = ({ currentQuestion, questions }) => (
  <div className="flex justify-between mb-4">
    {questions.map((_, index) => (
      <div
        key={index}
        className={`w-8 h-8 rounded-full flex items-center justify-center ${
          index <= currentQuestion ? 'bg-[#00ff9d] text-black' : 'bg-gray-600 text-white'
        }`}
      >
        {index + 1}
      </div>
    ))}
  </div>
);

// Componente de tooltip (dica de ferramenta) personalizado, usado para exibir informaes detalhadas quando o usurio passa o mouse sobre um elemento grfico.
// S  mostrado quando ativo (active) e quando h dados para exibir (payload).
// A tooltip exibe informaes sobre 'Days to Complete', 'Urgency', e 'Importance' de um item.
export const CustomTooltip = ({ active, payload }) => {
  if (active && payload && payload.length) {
    const data = payload[0].payload;
    return (
      <div className="custom-tooltip" style={{ backgroundColor: '#1a1a1a', border: '1px solid #00ff9d', padding: '10px', borderRadius: '5px' }}>
        {/* Exibe o nome do item */}
        <p className="label" style={{ color: '#00ff9d' }}>{`${data.name}`}</p>
        {/* Exibe o nmero de dias para completar */}
        <p style={{ color: '#fff' }}>{`Days to Complete: ${data.daysToComplete}`}</p>
        {/* Exibe a urgncia do item */}
        <p style={{ color: '#fff' }}>{`Urgency: ${data.urgency}`}</p>
        {/* Exibe a importncia do item */}
        <p style={{ color: '#fff' }}>{`Importance: ${data.importance}`}</p>
      </div>
    );
  }
  return null; // Retorna null se a tooltip no estiver ativa ou no houver dados.
};
