// ImportExportButtons.js

// Importa o React, uma biblioteca JavaScript usada para construir interfaces de usurio.
import React from 'react';

// Importa o componente Button de um arquivo local, que provavelmente  um boto estilizado de acordo com o design do projeto.
import { Button } from './components';

// Importa os cones de download e upload da biblioteca 'lucide-react', que so usados para ilustrar os botes de exportar e importar.
import { Download, Upload } from 'lucide-react';

// Define um componente funcional React chamado ImportExportButtons.
// Este componente aceita algumas propriedades: onExport, onImport, t e className.
// - onExport: uma funo que ser executada quando o boto de exportar for clicado.
// - onImport: uma funo que ser executada quando o boto de importar for clicado.
// - t: um objeto de traduo, usado para definir os textos dos botes.
// - className: uma string opcional para adicionar classes CSS extras ao componente principal.
const ImportExportButtons = ({ onExport, onImport, t, className }) => {
  return (
    // Retorna um continer <div> que organiza os botes em uma linha, centralizados e com espao entre eles.
    // `className`  utilizado para permitir que classes CSS adicionais sejam aplicadas externamente.
    <div className={`flex justify-center space-x-2 mb-4 ${className}`}>
      
      {/* Primeiro boto: boto de exportar */}
      <Button
        onClick={onExport} // Chama a funo onExport quando o boto  clicado.
        className="bg-[#374151] hover:bg-[#00864c] text-white flex items-center justify-center py-1.5 px-3 transition-colors duration-300" // Define a aparncia e o comportamento do boto.
      >
        {/* cone de download antes do texto */}
        <Download className="mr-1 h-4 w-4 inline-block align-middle" />
        {/* Texto do boto, que  passado atravs da propriedade t.export */}
        <span className="align-middle">{t.export}</span>
      </Button>

      {/* Segundo boto: boto de importar */}
      <Button
        onClick={onImport} // Chama a funo onImport quando o boto  clicado.
        className="bg-[#374151] hover:bg-[#00864c] text-white flex items-center justify-center py-1.5 px-3 transition-colors duration-300" // Define a aparncia e o comportamento do boto.
      >
        {/* cone de upload antes do texto */}
        <Upload className="mr-1 h-4 w-4 inline-block align-middle" />
        {/* Texto do boto, que  passado atravs da propriedade t.import */}
        <span className="align-middle">{t.import}</span>
      </Button>
    </div>
  );
};

// Exporta o componente ImportExportButtons para que ele possa ser usado em outros arquivos do projeto.
export default ImportExportButtons;
