import React, { useState, useRef, useEffect } from "react";
import {
  Brain,
  ChevronDown,
  Plus,
  Trash2,
  Send,
  CheckCircle,
} from "lucide-react";
import {
  Button,
  Input,
  Textarea,
  Card,
  CardHeader,
  CardContent,
  CardFooter,
  CardTitle,
  Alert,
  AlertTitle,
  AlertDescription,
  DisclaimerModal,
} from "./HelperComponents"; // Importa componentes auxiliares
import { calculateWeightedScores, ImprovedScatterChart } from "./DecisionLogic"; // Importa funes e componentes relacionados  lgica de deciso
import { translations } from "./translation"; // Importa tradues
import ImportExportButtons from "./ImportExportButtons"; // Importa o componente de botes de importao/exportao
import {
  exportDecisionProcess,
  importDecisionProcess,
} from "./DecisionMatrixExportImport"; // Importa funes de importao/exportao de decises
import ContactSection from "../../AppComponents/ContactSection";

const DecisionHelper = ({ language }) => {
  // Definio de estados utilizando o hook useState
  const [alternatives, setAlternatives] = useState([]); // Armazena as alternativas de deciso
  const [decision, setDecision] = useState(null); // Armazena a deciso final
  const [userInput, setUserInput] = useState(""); // Armazena a entrada do usurio
  const [criteria, setCriteria] = useState([
    { name: "Importance", weight: 5 },
    { name: "Urgency", weight: 4 },
  ]); // Armazena os critrios de deciso com pesos
  const [showMatrix, setShowMatrix] = useState(false); // Controla a exibio da matriz de deciso
  const [isDisclaimerOpen, setIsDisclaimerOpen] = useState(false); // Controla a exibio do modal de iseno de responsabilidade
  const [hasUnsavedChanges, setHasUnsavedChanges] = useState(false); // Indica se h alteraes no salvas

  const fileInputRef = useRef(null); // Referncia ao input de arquivo para importao

  // Obteno das tradues de acordo com o idioma selecionado
  const t = translations[language];

  // Hook useEffect para avisar o usurio sobre mudanas no salvas antes de sair da pgina
  useEffect(() => {
    const handleBeforeUnload = (e) => {
      if (hasUnsavedChanges) {
        e.preventDefault();
        e.returnValue = t.unsavedChangesWarning; // Exibe uma mensagem de aviso
      }
    };

    window.addEventListener("beforeunload", handleBeforeUnload);

    return () => {
      window.removeEventListener("beforeunload", handleBeforeUnload); // Remove o listener quando o componente  desmontado
    };
  }, [hasUnsavedChanges, t]);

  // Marca que h alteraes no salvas quando alternativas ou critrios so alterados
  useEffect(() => {
    setHasUnsavedChanges(true);
  }, [alternatives, criteria]);

  // Adiciona uma nova alternativa ao estado
  const addAlternative = () => {
    setAlternatives([
      ...alternatives,
      { name: "", scores: criteria.map(() => 0) },
    ]);
  };

  // Remove uma alternativa especfica
  const removeAlternative = (index) => {
    const newAlternatives = alternatives.filter((_, i) => i !== index);
    setAlternatives(newAlternatives);
  };

  // Atualiza o nome ou os scores de uma alternativa especfica
  const updateAlternative = (index, field, value) => {
    const newAlternatives = [...alternatives];
    if (field === "name") {
      newAlternatives[index].name = value;
    } else {
      newAlternatives[index].scores[field] = parseFloat(value);
    }
    setAlternatives(newAlternatives);
  };

  // Adiciona um novo critrio de deciso
  const addCriterion = () => {
    setCriteria([...criteria, { name: "", weight: 3 }]);
  };

  // Atualiza o nome ou o peso de um critrio especfico
  const updateCriterion = (index, field, value) => {
    const newCriteria = [...criteria];
    newCriteria[index][field] = field === "weight" ? parseFloat(value) : value;
    setCriteria(newCriteria);
  };

  // Remove um critrio especfico e atualiza as alternativas
  const removeCriterion = (index) => {
    const newCriteria = criteria.filter((_, i) => i !== index);
    setCriteria(newCriteria);
    setAlternatives(
      alternatives.map((alt) => ({
        ...alt,
        scores: alt.scores.filter((_, i) => i !== index),
      }))
    );
  };

  // Realiza a deciso final com base nos critrios e alternativas
  const makeDecision = () => {
    if (alternatives.length < 2) {
      setDecision(t.addTwoAlternatives); // Verifica se h pelo menos duas alternativas
      return;
    }

    const weightedScores = calculateWeightedScores(alternatives, criteria); // Calcula os scores ponderados
    const bestAlternative = weightedScores.reduce((prev, current) =>
      current.score > prev.score ? current : prev
    );

    // Define a melhor alternativa com base nos scores calculados
    setDecision(
      t.bestAlternative
        .replace("{name}", bestAlternative.name)
        .replace("{score}", bestAlternative.score.toFixed(2))
    );
  };

  // Processa a entrada do usurio e sugere alternativas
  const processUserInput = () => {
    const words = userInput.toLowerCase().split(/\s+/);
    const extractedAlternatives = [];
    let currentAlternative = { name: "", scores: [] };

    words.forEach((word, index) => {
      if (word !== "and" && word !== "or" && word !== "e" && word !== "ou") {
        currentAlternative.name += (currentAlternative.name ? " " : "") + word;
      }

      if (
        index === words.length - 1 ||
        ["and", "or", "e", "ou"].includes(words[index + 1])
      ) {
        if (currentAlternative.name) {
          currentAlternative.scores = criteria.map(
            () => Math.floor(Math.random() * 5) + 1
          );
          extractedAlternatives.push(currentAlternative);
          currentAlternative = { name: "", scores: [] };
        }
      }
    });

    setAlternatives(extractedAlternatives);
    setUserInput("");
    suggestCriteria(words); // Sugere critrios com base nas palavras-chave na entrada do usurio
  };

  // Sugere critrios de deciso com base em palavras comuns
  const suggestCriteria = (words) => {
    const commonCriteria = [
      "cost",
      "time",
      "quality",
      "risk",
      "benefit",
      "impact",
      "viability",
      "durability",
      "satisfaction",
      "innovation",
      "custo",
      "tempo",
      "qualidade",
      "risco",
      "benefcio",
      "impacto",
      "viabilidade",
      "durabilidade",
      "satisfao",
      "inovao",
    ];
    const suggestedCriteria = commonCriteria.filter((criterion) =>
      words.includes(criterion)
    );

    if (suggestedCriteria.length > 0) {
      setCriteria([
        ...criteria,
        ...suggestedCriteria.map((name) => ({ name, weight: 3 })),
      ]);
    }
  };

  // Exibe o modal de iseno de responsabilidade antes de realizar a deciso
  const handleDecideClick = () => {
    setIsDisclaimerOpen(true);
  };

  // Confirma a deciso e fecha o modal de iseno
  const handleDisclaimerConfirm = () => {
    setIsDisclaimerOpen(false);
    makeDecision();
  };

  // Exporta o processo de deciso para um arquivo
  const handleExport = () => {
    exportDecisionProcess(alternatives, criteria, decision, t);
    setHasUnsavedChanges(false); // Marca que as mudanas foram salvas
  };

  // Dispara o input de arquivo para importar o processo de deciso
  const handleImportClick = () => {
    if (fileInputRef.current) {
      fileInputRef.current.click();
    }
  };

  return (
    <div className="w-full bg-black text-gray-300 font-sans antialiased">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
        {/* Mantendo ttulo e subttulo com a mesma visibilidade */}
        <h1 className="text-3xl font-bold text-[#00ff9d] text-center mb-2 hover:text-[#00cc7d] transition-colors">
          {t.title}
        </h1>
        <p className="text-lg text-[#f1f5f9] italic text-center mb-4">
          {t.subtitle}
        </p>

        {/* Main content container */}
        <div className="space-y-4 mb-4">
          {/* Grid layout for cards with reduced gap */}
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-4">
            {/* Carto para descrever o dilema */}
            <Card className="bg-[#1a1a1a] border-[#333333] shadow-[#00ff9d]/20">
              <CardHeader>
                <CardTitle className="text-[#f1f5f9] flex items-center">
                  <Brain className="mr-2" /> {t.describeDilemma}
                </CardTitle>
              </CardHeader>
              <CardContent>
                <Textarea
                  className="w-full bg-[#111111] text-gray-300 border-[#333333] focus:border-[#00ff9d] focus:ring-[#00ff9d] rounded-md"
                  placeholder={t.dilemmaPlaceholder}
                  value={userInput}
                  onChange={(e) => setUserInput(e.target.value)}
                />
              </CardContent>
              <CardFooter>
                <Button
                  onClick={processUserInput}
                  className="w-full bg-[#374151] hover:bg-[#00864c] text-white font-medium flex items-center justify-center space-x-2 py-2 px-4 rounded transition-colors duration-300"
                >
                  <Send className="h-4 w-4" />
                  <span>{t.analyzeOptions}</span>
                </Button>
              </CardFooter>
            </Card>

            {/* Carto para adicionar e gerenciar critrios de deciso */}
            <Card className="bg-[#1a1a1a] border-[#333333] shadow-[#00ff9d]/20">
              <CardHeader>
                <CardTitle className="text-[#f1f5f9]">
                  {t.decisionCriteria}
                </CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  {criteria.map((criterion, index) => (
                    <div key={index} className="flex items-center space-x-2">
                      <span className="text-[#00ff9d] font-bold min-w-[24px]">
                        {index + 1}.
                      </span>
                      <Input
                        className="flex-grow bg-[#111111] text-gray-300 border-[#333333] focus:border-[#00ff9d] focus:ring-[#00ff9d] rounded-md"
                        placeholder={t.criterionName}
                        value={criterion.name}
                        onChange={(e) =>
                          updateCriterion(index, "name", e.target.value)
                        }
                      />
                      <Input
                        className="w-24 bg-[#111111] text-gray-300 border-[#333333] focus:border-[#00ff9d] focus:ring-[#00ff9d] rounded-md"
                        type="number"
                        placeholder={t.weight}
                        value={criterion.weight}
                        onChange={(e) =>
                          updateCriterion(index, "weight", e.target.value)
                        }
                      />
                      <Button
                        onClick={() => removeCriterion(index)}
                        className="bg-red-900 hover:bg-red-800 rounded-full p-2 transition-colors durao-300"
                      >
                        <Trash2 className="h-4 w-4" />
                      </Button>
                    </div>
                  ))}
                </div>
              </CardContent>
              <CardFooter>
                <Button
                  onClick={addCriterion}
                  className="w-full bg-[#374151] hover:bg-[#00864c] text-white font-medium flex items-center justify-center space-x-2 py-2 px-4 rounded transition-colors durao-300"
                >
                  <Plus className="h-4 w-4" />
                  <span>{t.addCriterion}</span>
                </Button>
              </CardFooter>
            </Card>
          </div>

          {/* Carto para adicionar e gerenciar alternativas - largura total */}
          <Card className="bg-[#1a1a1a] border-[#333333] shadow-[#00864c]/20 mt-4">
            <CardHeader>
              <CardTitle className="text-[#00ff9d]">{t.alternatives}</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {alternatives.map((alt, altIndex) => (
                  <div key={altIndex} className="space-y-2">
                    <div className="flex items-center space-x-2">
                      <span className="text-[#00ff9d] font-bold min-w-[24px]">
                        {altIndex + 1}.
                      </span>
                      <Input
                        className="flex-grow bg-[#111111] text-gray-300 border-[#333333] focus:border-[#00ff9d] focus:ring-[#00864c] rounded-md"
                        placeholder={t.alternativeName}
                        value={alt.name}
                        onChange={(e) =>
                          updateAlternative(altIndex, "name", e.target.value)
                        }
                      />
                      <Button
                        onClick={() => removeAlternative(altIndex)}
                        className="bg-red-900 hover:bg-red-800 rounded-full p-2 transition-colors duration-300"
                      >
                        <Trash2 className="h-4 w-4" />
                      </Button>
                    </div>
                    <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4 ml-8">
                      {criteria.map((criterion, critIndex) => (
                        <div
                          key={critIndex}
                          className="flex items-center space-x-2"
                        >
                          <span className="text-sm text-gray-400">
                            {criterion.name}:
                          </span>
                          <Input
                            className="flex-grow bg-[#111111] text-gray-300 border-[#333333] focus:border-[#00ff9d] focus:ring-[#00ff9d] rounded-md"
                            type="number"
                            placeholder={t.score}
                            value={alt.scores[critIndex]}
                            onChange={(e) =>
                              updateAlternative(
                                altIndex,
                                critIndex,
                                e.target.value
                              )
                            }
                          />
                        </div>
                      ))}
                    </div>
                  </div>
                ))}
              </div>
            </CardContent>
            <CardFooter className="flex justify-between items-center space-x-4">
              <Button
                onClick={addAlternative}
                className="flex items-center text-white hover:text-[#00ff9d] hover:underline cursor-pointer"
              >
                <Plus className="mr-2 h-4 w-4" /> {t.addAlternative}
              </Button>
              <Button
                onClick={handleDecideClick}
                className="mx-auto w-1/2 bg-[#00864c] hover:bg-[#00ff9d] text-black font-medium py-3 px-6 rounded transition-colors duration-300 flex items-center justify-center"
              >
                <CheckCircle className="mr-2 h-5 w-5" /> {t.decide}
              </Button>
            </CardFooter>
          </Card>

          {/* Carto que exibe a matriz de deciso em um grfico - largura total */}
          <Card className="bg-[#0a0a0a] border-[#333333] shadow-[#00ff9d]/20 mt-4 overflow-hidden">
            <CardHeader>
              <CardTitle
                onClick={() => setShowMatrix(!showMatrix)}
                className="text-[#00ff9d] flex items-center justify-between cursor-pointer"
              >
                {t.decisionMatrix}
                <ChevronDown
                  className={`transition-transform ${
                    showMatrix ? "rotate-180" : ""
                  }`}
                />
              </CardTitle>
            </CardHeader>
            {showMatrix && (
              <CardContent className="p-0">
                <ImprovedScatterChart
                  alternatives={alternatives}
                  criteria={criteria}
                  calculateWeightedScores={calculateWeightedScores}
                  language={language}
                />
              </CardContent>
            )}
          </Card>

          {/* Exibe o resultado da deciso final */}
          {decision && (
            <Alert className="mt-4 bg-[#1a1a1a] border-[#00ff9d] text-[#00ff9d]">
              <AlertTitle className="text-[#00ff9d] font-bold">
                {t.finalDecision}
              </AlertTitle>
              <AlertDescription className="text-[#00ff9d]">
                {decision}
              </AlertDescription>
            </Alert>
          )}

          {/* Import/Export buttons */}
          <div className="flex justify-center mb-2">
            <ImportExportButtons
              onExport={handleExport}
              onImport={handleImportClick}
              language={language}
              className="flex items-center space-x-2"
            />
          </div>

          <input
            type="file"
            ref={fileInputRef}
            style={{ display: "none" }}
            onChange={(e) => {
              importDecisionProcess(
                e,
                setAlternatives,
                setCriteria,
                setDecision,
                t
              );
              setHasUnsavedChanges(false);
            }}
            accept=".json"
          />

          {/* Contact Section mais prximo do footer */}
          <div className="mt-2">
            <ContactSection />
          </div>

          {/* Disclaimer Modal */}
          <DisclaimerModal
            isOpen={isDisclaimerOpen}
            onClose={() => setIsDisclaimerOpen(false)}
            onConfirm={handleDisclaimerConfirm}
            language={language}
          />
        </div>
      </div>
    </div>
  );
};

export default DecisionHelper; // Exporta o componente para uso em outras partes da aplicao
