import React from 'react';
import { Button } from './HelperComponents'; // Importa o componente de boto personalizado
import { Download, Upload } from 'lucide-react'; // Importa os cones de download e upload da biblioteca 'lucide-react'
import { translations } from './translation'; // Importa as tradues de texto

// Componente funcional ImportExportButtons, que renderiza dois botes para exportar e importar dados.
// Recebe as seguintes props:
// - onExport: funo a ser chamada quando o boto de exportar for clicado
// - onImport: funo a ser chamada quando o boto de importar for clicado
// - language: string que define o idioma atual (usado para selecionar a traduo correta)
// - className: string opcional para adicionar classes CSS adicionais ao continer dos botes
const ImportExportButtons = ({ onExport, onImport, language, className }) => {
  // Seleciona o conjunto de tradues apropriado com base no idioma atual
  const t = translations[language];

  return (
    // Continer <div> para os botes, com classes CSS para centralizar os botes e adicionar espaamento entre eles
    <div className={`flex justify-center space-x-2 mb-4 ${className}`}>
      
      {/* Boto de exportao */}
      <Button
        onClick={onExport} // Chama a funo onExport passada como prop quando o boto  clicado
        className="bg-[#0a0a0a] hover:bg-[#00864c] text-white flex items-center justify-center py-1.5 px-3" // Estilizao do boto
      >
        {/* cone de download */}
        <Download className="mr-1 h-4 w-4 inline-block align-middle" />
        {/* Texto do boto, definido pela traduo selecionada */}
        <span className="align-middle">{t.export}</span>
      </Button>
      
      {/* Boto de importao */}
      <Button
        onClick={onImport}  // Chama a funo onImport passada como prop quando o boto  clicado
        className="bg-[#0a0a0a] hover:bg-[#00864c] text-white flex items-center justify-center py-1.5 px-3" // Estilizao do boto
      >
        {/* cone de upload */}
        <Upload className="mr-1 h-4 w-4 inline-block align-middle" />
        {/* Texto do boto, definido pela traduo selecionada */}
        <span className="align-middle">{t.import}</span>
      </Button>
    </div>
  );
};

export default ImportExportButtons; // Exporta o componente para uso em outras partes da aplicao
